import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { changeField, initializeForm, register } from '../reducers/auth';
import './RegisterPage.scss';

const RegisterPage = ({ history }) => {
  const dispatch = useDispatch();
  const { form, regiCheck, regiError } = useSelector(({ auth }) => ({
    form: auth.register,
    regiCheck: auth.regiCheck,
    regiError: auth.regiError,
  }));

  const onChange = (e) => {
    const { value, name } = e.target;
    dispatch(
      changeField({
        form: 'register',
        key: name,
        value,
      }),
    );
  };

  const onSubmit = (e) => {
    e.preventDefault();
    const { name, password, email } = form;
    dispatch(register({ name, email, password }));
  };

  useEffect(() => {
    if (regiError) {
      alert(' ');
      return;
    }
    if (regiCheck) {
      history.push('/login');
    }
  }, [regiCheck, regiError, history]);

  useEffect(() => {
    dispatch(initializeForm('register'));
  }, [dispatch]);

  return (
    <div className="registerPage">
      <header className="register-title">
        <h1 className="register-logo">
          <img src="/images/coupang-logo.jpg" alt="coupang logo" />
        </h1>
      </header>
      <main className="register-main">
        <div className="register-content">
          <p> </p>
          <form onSubmit={onSubmit}>
            <div className="register-content">
              <input
                name="email"
                type="text"
                placeholder="()"
                onChange={onChange}
                value={form.email || ''}
              />
              <input
                type="password"
                name="password"
                placeholder=""
                onChange={onChange}
                value={form.password || ''}
              />
              <input
                type="password"
                name="passwordConfirm"
                placeholder=" "
                onChange={onChange}
                value={form.passwordConfirm || ''}
              />
              <input
                type="text"
                name="name"
                placeholder=""
                onChange={onChange}
                value={form.name || ''}
              />
              <input
                type="text"
                name="phoneNum"
                placeholder=" "
                onChange={onChange}
                value={form.phoneNum || ''}
              />
            </div>
            <div>
              <button className="register-submit"> </button>
            </div>
          </form>
        </div>
      </main>
      <footer className="register-footer">
        <p className="register-copy">Coupang Corp. All rights reserved</p>
      </footer>
    </div>
  );
};

export default RegisterPage;

