import React from 'react';
import { Link } from 'react-router-dom';
import './Navbar.scss';

const Navbar = () => {
  return (
    <div className="navbar">
      <ul className="navbar-item">
        <li>
          <Link to="/"></Link>
        </li>
        <li>
          <Link to="/search"></Link>
        </li>
        <li>
          <Link to="/orderHistory"></Link>
        </li>
        <li>
          <Link to="/favorite"></Link>
        </li>
        <li>
          <Link to="/profile"></Link>
        </li>
      </ul>
    </div>
  );
};

export default Navbar;
